import React from 'react'

export default function Navbar() {
    return (
        <>
            <nav className="navbar navbar-light bg-light justify-content-between">
                <a className="navbar-brand" href="https://manglasourav.github.io">
                    <img src={process.env.PUBLIC_URL + "/top-icon.png"} height="50px" alt={"SM"}></img>
                </a>
                <form className="form-inline">
                    <a type="button" className="btn btn-danger" href="https://drive.google.com/file/d/1iyahkr9Z1dr6OTtZ6YonCLnMsrDPLkhH/view"
                        style={{
                            backgroundColor: "rgba(175, 34, 34, 0.8)"
                        }}>
                        Resume
                        </a>
                </form>
            </nav>
        </>
    )
}

